import React from 'react'
import Layout from '../components/layout'

const Hire = ({ siteTitle }) => (
  <Layout>
    <div style={{marginLeft: "10%", paddingTop: "2%"}}>
      <p><span style={{color: "red"}}>*</span> <span className="underline">Got an idea</span> and wanna see it in reality ?</p>
      <p><span style={{color: "red"}}>*</span> Got some <span className="underline">work to share</span>?</p>
      <p><span style={{color: "red"}}>*</span> Wanna <span className="underline">have me in the conference</span> for a talk ?</p>
      <p><span style={{color: "red"}}>*</span> Or just wanna <span className="underline">have a coffee</span> with me ?</p>
      <p  style={{marginTop: 50}}>Feel free to drop a message. Typically replies in a day( or even less)</p>
      <div style={{border: '2px dashed yellow', padding: "4%", marginRight: '30%'}}>
        <p>Name:</p><input style={{width: "100%"}} type="text" />
        <p style={{marginTop: 20}}>EMail:</p><input style={{width: "100%"}} type="text" />
        <p style={{marginTop: 20}}>Message:</p><textarea rows="8" style={{width:"100%"}}></textarea><br />
        <button style={{marginTop: 20, backgroundImage: "linear-gradient(120deg, #ecb622 0%,#c9cc14 100%)", outline:0, height: "60px", width: "100%"}} type="button">Shoot !</button>
      </div>
      <div style={{marginTop: 30}}>
      Location:<i className="fa fa-map-pin" style={{color: "red", marginLeft: 10}}><span style={{color: 'white', marginLeft: 5}}>Hyderabad, India</span></i>
      </div>
    </div>
  </Layout>
  
)

export default Hire
